import { Component, OnInit } from "@angular/core";
import { DBService } from "../db-service.service";

@Component({
  selector: "app-wallet",
  templateUrl: "./wallet.component.html",
  styleUrls: ["./wallet.component.css"]
})
export class WalletComponent implements OnInit {
  constructor(private db: DBService) {}

  renderDelay = 200;
  formats = [
    "CODE128",
    "UPC",
    "EAN13",
    "EAN8",
    "EAN5",
    "EAN2",
    "CODE39",
    "ITF14",
    "MSI",
    "pharmacode",
    "codabar"
  ];

  cards = [];
  async ngOnInit() {
    this.cards = (await this.db.getAll("cards")) as [];
    console.log(this.cards);
    setTimeout(() => {
      for (let card of this.cards) {
        this.renderCard(card);
      }
    }, this.renderDelay);
  }

  newCard = {
    id: Math.floor(Math.random() * 100000),
    name: "new card",
    code: {
      value: 123456789999,
      format: "UPC",
      color: "#4b8b7f",
      lineColor: "black",
      width: 2,
      height: 100,
      displayValue: true,
      background: "transparent"
    }
  };

  updateNewCard() {
    this.renderCard(this.newCard);
  }

  addCard() {
    console.log("addCard()");
    // let card = {
    //   id: Math.floor(Math.random() * 100000),
    //   name: "new card",
    //   code: {
    //     value: 123456789999,
    //     format: "UPC",
    //     color: "black",
    //     displayValue: true,
    //     background: "transparent"
    //   }
    // };
    setTimeout(() => {
      for (let card of this.cards) {
        this.renderCard(card);
      }
    }, this.renderDelay);
    this.cards.push(this.newCard);
    this.db.add("cards", this.newCard);
  }

  renderCard(card) {
    try {
      let code = card.code;
      console.log("#barcode-" + card.id);
      (window as any).JsBarcode("#barcode-" + card.id, code.value, {
        format: code.format,
        lineColor: code.lineColor,
        width: code.width,
        height: code.height,
        displayValue: code.displayValue,
        background: code.background
      });
    } catch (error) {
      console.error(error);
    }
  }

  timeDown;
  mouseDown(card) {
    this.timeDown = Date.now();
  }
  mouseUp(card) {
    if (Date.now() - this.timeDown > 1000) {
      if (confirm(`Delete this card?\n${card.name} (${card.id})`)) {
        this.db.deleteItem("cards", card.id);
        this.ngOnInit();
      }
    }
  }
}
