import { Injectable } from '@angular/core';

const DB_NAME = "wallet";
const DB_VERSION = 1;

const promiseGenerator = () => {
  var resolve, reject;
  var promise = new Promise(function (_resolve, _reject) {
    resolve = _resolve;
    reject = _reject;
  });
  return {
    promise: promise,
    resolve: resolve,
    reject: reject
  }
}

@Injectable({
  providedIn: 'root'
})
export class DBService {

  constructor() {
    // this.initDB(); 
    // don't init here as it cannot be awaited, and we don't want to have requests coming in before our db is ready
    // at the time of writing IndexedDB (IDBDatabase) does locking internally so we don't need to worry about locking :)
  }

  private static db: IDBDatabase;

  private static async initDB() {
    if (!window.indexedDB) {
      console.error("This browser does not support indexedDB for offline storage!!!");
      return;
    }
    if (this.db) {
      return;
    }

    let p = promiseGenerator();
    let request = indexedDB.open(DB_NAME, DB_VERSION);

    request.onerror = (event) => {
      p.reject(event);
    };

    request.onupgradeneeded = (event) => {
      console.log('DB Upgradining');
      console.log(event);
      let db = request.result;

      if (event.oldVersion < 1) {
        let tabs = db.createObjectStore("cards", { keyPath: "id" });
        tabs.createIndex("name", "name", { unique: false });
      }

      //if you need to update db structure add one to DB_VERSION and add the structure to a new oldVersion if statement
    };

    let self = this;
    request.onsuccess = () => {
      self.db = request.result;
      p.resolve(request);
    }

    return p.promise;
  }

  public async add(table: string, obj: any) {
    if (!DBService.db) {
      await DBService.initDB();
    }

    let p = promiseGenerator();
    let request = DBService.db.transaction(table, 'readwrite').objectStore(table).add(obj);

    request.onsuccess = () => {
      p.resolve(request.result);
    }
    request.onerror = () => {
      p.reject(request.error);
    }
    return p.promise;
  }

  public async get(table: string, key: any) {
    if (!DBService.db) {
      await DBService.initDB();
    }

    let p = promiseGenerator();
    let request = DBService.db.transaction(table).objectStore(table).get(key);

    request.onsuccess = () => {
      p.resolve(request.result);
    }
    request.onerror = () => {
      p.reject(request.error);
    }
    return p.promise;
  }

  public async getAll(table: string) {
    if (!DBService.db) {
      await DBService.initDB();
    }

    let p = promiseGenerator();
    var transaction = DBService.db.transaction(table, 'readonly');
    var objectStore = transaction.objectStore(table) as any;
    var request;

    if (objectStore.getAll) {
      // IDBObjectStore.getAll() will return the full set of items in our store.
      //console.log("browser does have getAll()")
      request = objectStore.getAll();

      request.onsuccess = () => {
        p.resolve(request.result);
      }
      request.onerror = () => {
        p.reject(request.error);
      }

    } else {
      // Fallback to the traditional cursor approach if getAll isn't supported.
      console.log("browser doesn't have objectStore.getAll()")
      var items = [];
      request = objectStore.openCursor();

      request.onsuccess = () => {
        var cursor = request.result;
        if (cursor) {
          items.push(cursor.value);
          cursor.continue();
        } else {
          p.resolve(items);
        }
      }
      request.onerror = () => {
        p.reject(request.error);
      }
    }
    return p.promise;
  }

  public async deleteItem(table: string, key: any) {
    if (!DBService.db) {
      await DBService.initDB();
    }

    let p = promiseGenerator();
    let request = DBService.db.transaction(table, 'readwrite').objectStore(table).delete(key);

    request.onsuccess = () => {
      p.resolve(request.result);
    }
    request.onerror = () => {
      p.reject(request.error);
    }
    return p.promise;
  }
}
